.section {
   width: 100%;
   margin-top: 25px;
   padding: 0 25px;  
   &-middle { text-align: center; }
   &-fluid { padding: 0; width: 100%; margin-top: 25px; }
   .img-section-2 { text-align: center; margin-top: 25px; }
   .section-3{
      &-left, &-right, &-middle {
         width: 30%;
         display: inline-block;
         vertical-align: middle;
         position: relative;
      }
      &-middle { float: left; width: 40%; }
      &-left { 
         float: left; 
         .section-icon {
            &-left { right: 0; }
            &-right {
               padding-right: 85px;
               text-align: right;
            }
         }
      }

      &-right {
         float: right; 
         .section-icon {
            &-left { left: 0; }
            &-right {
               padding-left: 85px;
            }
         }
      }
   }
   .animation {
      -webkit-animation-duration: 2s;
      animation-duration: 2s;
      animation-fill-mode: both;
      -webkit-animation-fill-mode: both;
   }
  

   .section-icon {
      margin: 155px 0;
      position: inherit;
      h4 {font-size: 20px;}
      p { margin-top: 10px; line-height: 26px; }
      &-left { position: absolute; top: 0; i { font-size: 50px; } }
      &:nth-child(2) { margin-bottom: 50px;  }
   }

   .section-album-image {
      &-square, &-rectangle, &-vertical {
         float: left;
         background-color: white;
         margin: 5px;
         border-radius: 5%;
         // background: url('../../img/bar-bartender-beer-commerce.jpg') no-repeat center;
         background-size: cover;
         background-attachment: fixed;
      }
      &-square {
         width: 24.9%;
         height: 295px;
      }

      &-rectangle {
         width: 48%;
         height: 295px;
      }

      &-square:last-child, &-rectangle:first-child { margin-right: 0; }

      &-vertical{
         margin-left: 0;
         height: 598px; //570px
         width: 25%;
      }
   }
}